<template>
  <el-drawer
    size="700"
    title="查看"
    class="custom-drawer"
    :model-value="initData.visible"
    @closed="handleClose"
  >
    <div class="body-container">
      <div class="body-content" ref="bodyContentRef">
        <el-form ref="formRef" label-width="100px" label-position="top">
          <div v-for="(item, index) in formData.componentList" :key="index">
            <!-- 单行输入框 -->
            <template v-if="item.propertyType === 1">
              <el-form-item
                :label="item.tagName"
                :prop="'componentList.' + index + '.value'"
              >
                <div
                  v-if="item.propertyKey === 'business_label'"
                  class="business-label"
                >
                  <div class="label-list">
                    <span
                      v-for="(labelItem, labelIndex) in item.labelText"
                      :key="labelIndex"
                      >{{ labelItem }}</span
                    >
                  </div>
                </div>
                <template v-else>
                  <el-input
                    disabled
                    :model-value="(item.value || '') + ' ' + (item.unit || '')"
                  ></el-input>
                </template>
              </el-form-item>
            </template>

            <!-- 多行输入框 -->
            <template v-if="item.propertyType === 2">
              <el-form-item
                :label="item.tagName"
                :prop="'componentList.' + index + '.value'"
              >
                <span v-if="item.propertyKey === 'verbal_trick'">{{
                  item.value
                }}</span>
                <el-input
                  v-else
                  readonly
                  type="textarea"
                  :rows="3"
                  :model-value="item.value"
                ></el-input>
              </el-form-item>
            </template>
            <!-- 数字输入框 -->
            <template v-if="item.propertyType === 7">
              <el-form-item
                :label="item.tagName"
                :prop="'componentList.' + index + '.value'"
              >
                <el-input
                  disabled
                  :model-value="(item.value || '') + ' ' + (item.unit || '')"
                ></el-input>
              </el-form-item>
            </template>

            <!-- 单选框 -->
            <template v-if="item.propertyType === 4">
              <el-form-item
                :label="item.tagName"
                :prop="'componentList.' + index + '.value'"
              >
                <el-input disabled :model-value="item.value"></el-input>
              </el-form-item>
            </template>

            <!-- 单选输入 -->
            <template v-if="item.propertyType === 11">
              <el-form-item
                :label="item.tagName"
                :prop="'componentList.' + index + '.value'"
              >
                <el-input disabled :model-value="item.value"></el-input>
              </el-form-item>
            </template>

            <!-- 多选框 -->
            <template v-if="item.propertyType === 5">
              <el-form-item
                :label="item.tagName"
                :prop="'componentList.' + index + '.value'"
              >
                <el-input disabled :model-value="item.value"></el-input>
              </el-form-item>
            </template>

            <!-- 多选输入框 -->
            <template v-if="item.propertyType === 12">
              <el-form-item
                :label="item.tagName"
                :prop="'componentList.' + index + '.value'"
              >
                <template v-if="item.value && item.value.length">
                  <span v-for="(v, k) in item.value" :key="k"
                    >{{ v.name }}：{{ v.value }}&nbsp;&nbsp;&nbsp;&nbsp;</span
                  >
                </template>
                <template v-else>
                  <span>暂无</span>
                </template>
              </el-form-item>
            </template>

            <!-- 行业 -->
            <template v-if="item.propertyKey === 'industry'">
              <el-form-item
                :label="item.tagName"
                :prop="'componentList.' + index + '.value'"
              >
                <el-input disabled :model-value="item.value"></el-input>
              </el-form-item>
            </template>

            <!-- 附件 -->
            <template v-if="item.propertyType === 9">
              <el-form-item
                :label="item.tagName"
                :prop="'componentList.' + index + '.value'"
              >
                <el-link
                  v-if="item.fileList && item.fileList.length"
                  icon="el-icon-paperclip"
                  :underline="false"
                  type="primary"
                  @click="downloadAttachment(item)"
                >
                  <el-icon class="del-icon colorGray">
                    <Link />
                  </el-icon>
                  下载查看
                </el-link>
                <span v-else>暂无</span>
              </el-form-item>
            </template>

            <!-- 图片 -->
            <template v-if="item.propertyType === 13">
              <el-form-item
                :label="item.tagName"
                :prop="'componentList.' + index + '.value'"
              >
                <template v-if="item.fileList && item.fileList.length">
                  <div
                    class="picture-list"
                    v-for="(fileItem, fileIndex) in item.fileList"
                    :key="fileIndex"
                  >
                    <div class="picture-item">
                      <img :src="fileItem.url" alt="" />
                      <div class="preview">
                        <el-icon
                          @click="
                            previewImage(item.tagName, item.fileList, fileIndex)
                          "
                        >
                          <CirclePlus />
                        </el-icon>
                      </div>
                    </div>
                  </div>
                </template>
                <span v-else>暂无</span>
              </el-form-item>
            </template>
          </div>
        </el-form>
      </div>
    </div>
    <template #footer>
      <el-button @click="handleClose">关 闭</el-button>
    </template>
  </el-drawer>
  <CustomPreviewImage ref="previewImageRef"></CustomPreviewImage>
</template>
<script lang="ts" setup>
import { ref, unref, reactive } from "vue";
import { CirclePlus, Link } from "@element-plus/icons-vue";

import { downloadFile } from "@/api/download-api";
import {
  industryTreeApi,
  speechLibraryListApi,
  detailReportComponentListApi,
} from "@/api/business-management-api";
import { LocalStorage } from "jz-tool-lib";

const { bd_virCompanyId: virCompanyId } = LocalStorage.batchGetItem([
  "bd_virCompanyId",
]);
const formRef = ref();
const bodyContentRef = ref();
const previewImageRef = ref();
const keyWords = ref<any[]>([]);
const industryList = ref<any>([]);
const speechLibraryList = ref<any>([]);
const initData = reactive<{
  visible: boolean;
}>({
  visible: false,
});
const companyInfo = ref({
  id: "",
  companyId: "",
  companyCode: "",
  companyName: "",
});
const formData = reactive({
  componentList: [],
});

function previewImage(title: string, imgList: string[], index: number) {
  const list = imgList.map((item: any) => item.url);
  previewImageRef.value.trigger(true, title, list, index);
}
// 下载
const downloadAttachment = (info) => {
  const fileIds = info.fileList
    .map((item: Record<string, any>) => item.fileId)
    .join(",");
  const fileUrls = info.fileList
    .map((item: Record<string, any>) => item.url)
    .join(",");
  const params = {
    api: `call-rest/reported/companyCallPhoneAgencyCodeApply/downLoadTemplate?fileIds=${fileIds}&urls=${fileUrls}`,
  };
  downloadFile(params);
};
// 初始化
function getAllList() {
  Promise.all([getIndustryTree(), getSpeechLibraryList()]).then((res) => {
    industryList.value = res[0];
    speechLibraryList.value = res[1].list;
    queryEditComponentInfo();
  });
}

// 查询行业树
const getIndustryTree = async () => {
  const { data } = await industryTreeApi.getData();
  return industryTreeApi.handleData(data);
};

// 话术模板
const getSpeechLibraryList = async () => {
  const params = {
    pageSize: 100,
    pageNo: 1,
    companyCode: companyInfo.value.companyCode,
    companyId: virCompanyId,
  };
  const { data } = await speechLibraryListApi.getData(params);
  return speechLibraryListApi.handleData(data);
};

// 查看
const queryEditComponentInfo = async () => {
  const params = {
    id: companyInfo.value.id,
    companyId: companyInfo.value.companyId,
    sourceType: 0, // 0是手动，为1是电子化
  };
  const { data } = await detailReportComponentListApi.getData(params);
  const { list } = detailReportComponentListApi.handleData(
    data.componentList,
    industryList.value,
    speechLibraryList.value
  );
  (formData.componentList as any) = list;
};
// 关闭弹框
function handleClose() {
  keyWords.value = [];
  formData.componentList = [];
  unref(bodyContentRef).scrollTop = 0;
  initData.visible = false;
}
function trigger(
  visible: boolean,
  info: {
    id: string;
    companyId: string;
    companyCode: string;
    companyName: string;
  }
) {
  initData.visible = visible;
  companyInfo.value = info;
  if (visible) {
    getAllList();
  }
}

defineExpose({
  trigger,
});
</script>
<style lang="scss" scoped>
.key-words {
  .el-tag {
    margin-right: 10px;

    &:last-child {
      margin-right: 0;
    }
  }
}
.business-label {
  .label-list {
    @include flex(initial, initial, column);
    span {
      line-height: 28px;
    }
  }
}
.picture-list {
  display: flex;
  flex-wrap: wrap;

  .picture-item {
    position: relative;
    width: 90px;
    height: 90px;
    overflow: hidden;
    border-radius: 6px;

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: contain;
    }

    .preview {
      position: absolute;
      top: 0;
      left: 0;
      display: none;
      width: 100%;
      height: 100%;
      font-size: 18px;
      color: #ffffff;
      cursor: pointer;
      background: rgba(0, 0, 0, 0.5);
      border-radius: 6px;
    }

    &:hover {
      .preview {
        @include flex(center, center);
      }
    }
  }
}
</style>
